import React, { useState, useRef, useEffect } from 'react';
import io from 'socket.io-client';
import axios from 'axios';
import FingerprintJS from '@fingerprintjs/fingerprintjs';

const BASE_NODE_URL = process.env.REACT_APP_BASE_NODE_URL || "http://localhost:5000";

// Encryption utilities
const generateKeyPair = async () => {
  try {
    const keyPair = await window.crypto.subtle.generateKey(
      {
        name: "RSA-OAEP",
        modulusLength: 2048,
        publicExponent: new Uint8Array([1, 0, 1]),
        hash: "SHA-256",
      },
      true,
      ["encrypt", "decrypt"]
    );
    
    // Export the keys to JWK format
    const publicKey = await window.crypto.subtle.exportKey("jwk", keyPair.publicKey);
    const privateKey = await window.crypto.subtle.exportKey("jwk", keyPair.privateKey);
    
    return { publicKey, privateKey };
  } catch (error) {
    console.error('Error generating key pair:', error);
    throw error;
  }
};

const importPublicKey = async (jwkKey) => {
  try {
    return await window.crypto.subtle.importKey(
      "jwk",
      jwkKey,
      {
        name: "RSA-OAEP",
        hash: "SHA-256",
      },
      true,
      ["encrypt"]
    );
  } catch (error) {
    console.error('Error importing public key:', error);
    throw error;
  }
};

const importPrivateKey = async (jwkKey) => {
  try {
    return await window.crypto.subtle.importKey(
      "jwk",
      jwkKey,
      {
        name: "RSA-OAEP",
        hash: "SHA-256",
      },
      true,
      ["decrypt"]
    );
  } catch (error) {
    console.error('Error importing private key:', error);
    throw error;
  }
};

const encryptMessage = async (message, publicKey) => {
  try {
    const encoder = new TextEncoder();
    const data = encoder.encode(message);
    
    const importedPublicKey = await importPublicKey(publicKey);
    const encryptedData = await window.crypto.subtle.encrypt(
      {
        name: "RSA-OAEP"
      },
      importedPublicKey,
      data
    );
    
    // Convert to base64 for transmission
    return btoa(String.fromCharCode(...new Uint8Array(encryptedData)));
  } catch (error) {
    console.error('Error encrypting message:', error);
    throw error;
  }
};

const decryptMessage = async (encryptedMessage, privateKey) => {
  try {
    // Convert from base64
    const binaryString = atob(encryptedMessage);
    const bytes = new Uint8Array(binaryString.length);
    for (let i = 0; i < binaryString.length; i++) {
      bytes[i] = binaryString.charCodeAt(i);
    }
    
    const importedPrivateKey = await importPrivateKey(privateKey);
    const decryptedData = await window.crypto.subtle.decrypt(
      {
        name: "RSA-OAEP"
      },
      importedPrivateKey,
      bytes
    );
    
    const decoder = new TextDecoder();
    return decoder.decode(decryptedData);
  } catch (error) {
    console.error('Error decrypting message:', error);
    return '[Encrypted message - cannot decrypt]';
  }
};

// Initialize the fingerprint agent
const fpPromise = FingerprintJS.load();

function App() {
  const [username, setUsername] = useState('');
  const [recipient, setRecipient] = useState('');
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState([]);
  const [connected, setConnected] = useState(false);
  const [status, setStatus] = useState('Checking relay status...');
  const [deviceId, setDeviceId] = useState('');
  const [connectionDetails, setConnectionDetails] = useState({});
  const [showConnectionInfo, setShowConnectionInfo] = useState(false);
  const [securityAlert, setSecurityAlert] = useState(null);
  const [typing, setTyping] = useState(false);
  const [relayServerUrl, setRelayServerUrl] = useState('Unknown');
  const [relayStatus, setRelayStatus] = useState('checking');
  const [onlineUsers, setOnlineUsers] = useState([]);
  const [recipientStatus, setRecipientStatus] = useState({ exists: false, online: false });
  const [isCheckingUsername, setIsCheckingUsername] = useState(false);
  const [usernameAvailable, setUsernameAvailable] = useState(true);
  const [keyPair, setKeyPair] = useState(null);
  const [publicKeys, setPublicKeys] = useState({}); // username -> publicKey
  const [encryptionEnabled, setEncryptionEnabled] = useState(true);
  const [encryptionStatus, setEncryptionStatus] = useState('initializing');
  const socketRef = useRef(null);
  const messagesEndRef = useRef(null);
  const typingTimeoutRef = useRef(null);
  const pingIntervalRef = useRef(null);

  // Get device fingerprint and initialize encryption on component mount
  useEffect(() => {
    const initializeDevice = async () => {
      try {
        // Get device fingerprint
        const fp = await fpPromise;
        const result = await fp.get();
        const deviceFingerprint = result.visitorId;
        setDeviceId(deviceFingerprint);
        
        // Check for existing keys in localStorage
        const savedKeyPair = localStorage.getItem('whispernetKeyPair');
        if (savedKeyPair) {
          try {
            const parsedKeyPair = JSON.parse(savedKeyPair);
            setKeyPair(parsedKeyPair);
            setEncryptionStatus('ready');
            console.log('Loaded existing encryption keys');
          } catch (error) {
            console.error('Error parsing saved key pair:', error);
            generateNewKeyPair(deviceFingerprint);
          }
        } else {
          generateNewKeyPair(deviceFingerprint);
        }
        
        // Check relay status
        checkRelayStatus();
      } catch (error) {
        console.error('Error initializing device:', error);
        setEncryptionStatus('error');
      }
    };
    
    initializeDevice();
    
    return () => {
      if (socketRef.current) {
        socketRef.current.disconnect();
      }
      clearInterval(pingIntervalRef.current);
    };
  }, []);

  // Scroll to bottom of messages
  useEffect(() => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [messages]);

  // Check relay server status periodically when showing connection info
  useEffect(() => {
    if (connected && showConnectionInfo) {
      const relayInfoInterval = setInterval(() => {
        if (socketRef.current) {
          socketRef.current.emit('ping', { timestamp: Date.now() });
        }
      }, 5000);
      
      return () => clearInterval(relayInfoInterval);
    }
  }, [connected, showConnectionInfo, username]);

  const connectToBaseNode = () => {
    // Clear any previous connection
    if (socketRef.current) {
      socketRef.current.disconnect();
    }
    
    // Always start by connecting to the base node for initial handshake
    console.log('Connecting to base node for initial handshake:', BASE_NODE_URL);
    setStatus('Connecting to base node for relay discovery...');
    
    // Connect to base node
    socketRef.current = io(BASE_NODE_URL, {
      transports: ['websocket', 'polling'],
      reconnectionAttempts: 5,
      reconnectionDelay: 2000,
      query: { 
        deviceId,
        username 
      },
      forceNew: true
    });
    
    // Connection event handlers
    socketRef.current.on('connect', () => {
      console.log('Connected to base node with socket ID:', socketRef.current.id);
      setStatus('Connected to base node, discovering relays...');
      setRelayStatus('online');
      
      // Temporarily set as direct connection while we discover relays
      setRelayServerUrl(`Direct (${socketRef.current.id.substring(0, 8)}...)`);
      setConnectionDetails({
        socketId: socketRef.current.id,
        transport: socketRef.current.io.engine.transport.name,
        baseNodeUrl: BASE_NODE_URL,
        relayId: 'direct',
        relayStatus: 'direct_to_base'
      });
      
      // Get available relays first
      socketRef.current.emit('getAvailableRelays', {}, (response) => {
        console.log('Available relays:', response);
        
        if (response && response.relays && response.relays.length > 0) {
          // Cache relay information
          localStorage.setItem('whispernetRelayCache', JSON.stringify({
            timestamp: Date.now(),
            relays: response.relays
          }));
          
          // Connect to the first available relay
          connectToRelay(response.relays[0]);
        } else {
          // No relays available, register directly with base node
          console.log('No relays available, using base node as fallback');
          setStatus('No relay servers available, using base node as fallback');
          
          // Register with base node
          registerWithBaseNode();
          
          // Set up a listener for relay availability
          socketRef.current.on('relayAvailable', (relay) => {
            console.log('New relay available:', relay);
            
            // Ask the user if they want to switch to the relay
            setSecurityAlert({
              username: 'System',
              message: `A relay server is now available. Would you like to switch to it for better performance?`,
              type: 'info',
              action: 'switchToRelay',
              actionData: relay
            });
          });
        }
      });
    });
    
    // Handle connection errors
    socketRef.current.on('connect_error', (err) => {
      console.error('Base node connection error:', err.message);
      setStatus(`Base node connection failed: ${err.message}`);
      setRelayStatus('offline');
      
      // Try to use cached relay information if available
      const cachedRelayInfo = localStorage.getItem('whispernetRelayCache');
      if (cachedRelayInfo) {
        try {
          const parsedCache = JSON.parse(cachedRelayInfo);
          if (parsedCache.timestamp && (Date.now() - parsedCache.timestamp < 3600000)) { // Cache valid for 1 hour
            const cachedRelays = parsedCache.relays || [];
            if (cachedRelays.length > 0) {
              console.log('Base node unavailable, trying cached relay:', cachedRelays[0]);
              connectToRelay(cachedRelays[0], true); // true = fallback mode
              return;
            }
          }
        } catch (error) {
          console.error('Error parsing cached relay info:', error);
        }
      }
      
      setStatus('All connection attempts failed. Please try again later.');
    });
    
    // Handle disconnection
    socketRef.current.on('disconnect', (reason) => {
      console.log('Disconnected from base node:', reason);
      
      if (reason === 'io server disconnect' || reason === 'transport close') {
        setStatus('Disconnected from base node. Attempting to reconnect...');
      }
    });
  };
  
  // Function to connect to a relay server
  const connectToRelay = (relay, isFallback = false) => {
    if (!relay || !relay.id) {
      console.error('Invalid relay information');
      if (!isFallback) {
        registerWithBaseNode(); // Fall back to base node
      }
      return;
    }
    
    // Disconnect from current socket if connected
    if (socketRef.current) {
      // Save the current socket for later cleanup
      const oldSocket = socketRef.current;
      
      // Create a new socket for the relay connection
      socketRef.current = null;
      
      // Disconnect the old socket after we've established the new connection
      setTimeout(() => {
        console.log('Disconnecting from previous connection');
        oldSocket.disconnect();
      }, 1000);
    }
    
    const relayUrl = relay.id.startsWith('http') ? relay.id : `http://${relay.id}`;
    console.log(`Connecting to relay server: ${relayUrl}`);
    setStatus(`Connecting to relay server: ${relay.id}...`);
    
    // Connect to the relay
    socketRef.current = io(relayUrl, {
      transports: ['websocket', 'polling'],
      reconnectionAttempts: 3,
      reconnectionDelay: 2000,
      forceNew: true
    });
    
    // Set a timeout for relay connection
    const relayConnectionTimeout = setTimeout(() => {
      if (!socketRef.current || !socketRef.current.connected) {
        console.log('Relay connection timed out');
        
        if (!isFallback) {
          // Fall back to base node if this wasn't already a fallback attempt
          setStatus('Relay connection timed out, falling back to base node');
          connectDirectlyToBaseNode();
        } else {
          setStatus('All connection attempts failed. Please try again later.');
        }
      }
    }, 8000);
    
    // Handle successful connection
    socketRef.current.on('connect', () => {
      clearTimeout(relayConnectionTimeout);
      console.log(`Connected to relay server: ${relayUrl}`);
      setStatus(`Connected to relay server: ${relay.id}`);
      setRelayStatus('online');
      
      // Update UI with relay information
      setRelayServerUrl(relay.id);
      setConnectionDetails({
        socketId: socketRef.current.id,
        transport: socketRef.current.io.engine.transport.name,
        baseNodeUrl: BASE_NODE_URL,
        relayId: relay.id,
        relayStatus: 'connected_to_relay'
      });
      
      // Register with the relay
      socketRef.current.emit('register', { 
        username, 
        deviceId 
      }, (response) => {
        if (response && response.success) {
          setStatus('Registered with relay server');
          setConnected(true);
          
          // Set up socket event handlers
          connectSocketEvents();
          
          // Start ping interval
          startPingInterval();
          
          // Show success message
          setSecurityAlert({
            username: 'System',
            message: `Connected to relay server: ${relay.id}`,
            type: 'success'
          });
        } else {
          console.error('Failed to register with relay:', response);
          setStatus('Failed to register with relay server');
          socketRef.current.disconnect();
          
          if (!isFallback) {
            // Fall back to base node if this wasn't already a fallback attempt
            connectDirectlyToBaseNode();
          } else {
            setStatus('All connection attempts failed. Please try again later.');
          }
        }
      });
    });
    
    // Handle connection errors
    socketRef.current.on('connect_error', (err) => {
      clearTimeout(relayConnectionTimeout);
      console.error(`Relay connection error: ${err.message}`);
      setStatus(`Relay connection failed: ${err.message}`);
      
      if (!isFallback) {
        // Fall back to base node if this wasn't already a fallback attempt
        connectDirectlyToBaseNode();
      } else {
        setStatus('All connection attempts failed. Please try again later.');
      }
    });
  };
  
  // Function to connect directly to the base node as a fallback
  const connectDirectlyToBaseNode = () => {
    console.log('Connecting directly to base node as fallback:', BASE_NODE_URL);
    setStatus('Connecting to base node as fallback...');
    
    // Disconnect from current socket if connected
    if (socketRef.current) {
      socketRef.current.disconnect();
    }
    
    // Connect to base node
    socketRef.current = io(BASE_NODE_URL, {
      transports: ['websocket', 'polling'],
      reconnectionAttempts: 5,
      reconnectionDelay: 2000,
      query: { 
        deviceId,
        username 
      },
      forceNew: true
    });
    
    // Connection event handlers
    socketRef.current.on('connect', () => {
      console.log('Connected to base node with socket ID:', socketRef.current.id);
      setStatus('Connected to base node (fallback mode)');
      setRelayStatus('online');
      
      // Update UI to show direct connection
      setRelayServerUrl(`Direct (${socketRef.current.id.substring(0, 8)}...)`);
      setConnectionDetails({
        socketId: socketRef.current.id,
        transport: socketRef.current.io.engine.transport.name,
        baseNodeUrl: BASE_NODE_URL,
        relayId: 'direct',
        relayStatus: 'direct_to_base'
      });
      
      // Register with base node
      registerWithBaseNode();
      
      // Set up a listener for relay availability
      socketRef.current.on('relayAvailable', (relay) => {
        console.log('New relay available:', relay);
        
        // Ask the user if they want to switch to the relay
        setSecurityAlert({
          username: 'System',
          message: `A relay server is now available. Would you like to switch to it for better performance?`,
          type: 'info',
          action: 'switchToRelay',
          actionData: relay
        });
      });
    });
  };
  
  // The registerWithBaseNode function is used by the connectDirectlyToBaseNode function
  
  const registerWithBaseNode = () => {
    // Register with base node
    if (!socketRef.current || !socketRef.current.connected) {
      console.error('Cannot register: socket not connected');
      return;
    }
    
    socketRef.current.emit('registerUser', { 
      username, 
      deviceId 
    }, (response) => {
      console.log('Registration response:', response);
      if (response && response.success) {
        setStatus('Registered successfully');
        setConnected(true);
        
        // Set up socket event handlers
        connectSocketEvents();
        
        // Get initial data
        getOnlineUsers();
        
        // Start ping interval
        startPingInterval();
        
        // If we have a public key, send it to the server
        if (keyPair && keyPair.publicKey) {
          socketRef.current.emit('updatePublicKey', { 
            publicKey: keyPair.publicKey 
          }, (response) => {
            console.log('Public key update response:', response);
          });
        }
      } else {
        setStatus(`Registration failed: ${response?.reason || 'Unknown error'}`);
        setSecurityAlert({
          username: 'System',
          message: `Registration failed: ${response?.reason || 'Unknown error'}`,
          type: 'error'
        });
      }
    });
  };

  const handleDisconnect = () => {
    if (socketRef.current) {
      // Notify the server that we're intentionally disconnecting
      socketRef.current.emit('userLogout', { username, deviceId }, () => {
        console.log('Sent logout notification to server');
        
        // Now disconnect
        socketRef.current.disconnect();
        socketRef.current = null;
      });
    }
    
    // Clear local state
    setConnected(false);
    setMessages([]);
    setOnlineUsers([]);
    setRecipientStatus({ exists: false, online: false });
    setStatus('Disconnected');
    setRelayStatus('offline');
    clearInterval(pingIntervalRef.current);
  };
  
  const dismissAlert = (action, data) => {
    if (action && action !== 'dismiss') {
      // Handle specific actions
      if (action === 'switchToRelay' && data) {
        console.log('Switching to relay:', data);
        connectToRelay(data);
      }
    }
    
    // Clear the alert
    setSecurityAlert(null);
  };

  const retryConnection = () => {
    checkRelayStatus();
  };

  const checkRelayStatus = () => {
    setStatus('Checking relay status...');
    setRelayStatus('checking');
    
    // Try to connect to the base node
    connectToBaseNode();
  };

  const startPingInterval = () => {
    // Clear any existing interval
    if (pingIntervalRef.current) {
      clearInterval(pingIntervalRef.current);
    }
    
    // Set up a new interval
    pingIntervalRef.current = setInterval(() => {
      if (socketRef.current && socketRef.current.connected) {
        socketRef.current.emit('ping', { timestamp: Date.now() });
      } else {
        clearInterval(pingIntervalRef.current);
      }
    }, 30000); // Every 30 seconds
  };

  const generateNewKeyPair = async (deviceId) => {
    try {
      setEncryptionStatus('generating');
      const newKeyPair = await generateKeyPair();
      setKeyPair(newKeyPair);
      
      // Save to localStorage with device fingerprint
      localStorage.setItem('whispernetKeyPair', JSON.stringify(newKeyPair));
      localStorage.setItem('whispernetDeviceId', deviceId);
      
      setEncryptionStatus('ready');
      console.log('Generated new encryption keys');
    } catch (error) {
      console.error('Error generating new key pair:', error);
      setEncryptionStatus('error');
    }
  };

  const connectSocketEvents = () => {
    if (!socketRef.current) return;
    
    // Handle receiving messages
    socketRef.current.on('receiveMessage', async (data) => {
      console.log('Received message:', data);
      
      let messageContent = data.message;
      let decryptionStatus = 'plaintext';
      
      // Handle encrypted messages
      if (data.encrypted && keyPair && keyPair.privateKey) {
        try {
          decryptionStatus = 'decrypting';
          messageContent = await decryptMessage(data.message, keyPair.privateKey);
          decryptionStatus = 'decrypted';
        } catch (error) {
          console.error('Failed to decrypt message:', error);
          messageContent = '[Encrypted message - cannot decrypt]';
          decryptionStatus = 'failed';
        }
      }
      
      // Check if this is from a known contact with a different device
      if (data.fromDeviceId && publicKeys[data.from]) {
        const knownDeviceId = publicKeys[data.from].deviceId;
        if (knownDeviceId && knownDeviceId !== data.fromDeviceId) {
          setSecurityAlert({
            username: 'Security Alert',
            message: `⚠️ Identity mismatch for ${data.from}. This username was previously contacted from a different device. Confirm identity before proceeding.`,
            type: 'error'
          });
        }
      }
      
      // Store the sender's public key if provided
      if (data.publicKey && !publicKeys[data.from]) {
        setPublicKeys(prev => ({
          ...prev,
          [data.from]: {
            key: data.publicKey,
            deviceId: data.fromDeviceId,
            timestamp: Date.now()
          }
        }));
      }
      
      // Add message to state
      setMessages(prevMessages => [
        ...prevMessages,
        {
          from: data.from,
          message: messageContent,
          timestamp: data.timestamp || Date.now(),
          bounced: data.bounced || false,
          encrypted: data.encrypted || false,
          decryptionStatus
        }
      ]);
      
      // If this is from the current recipient, update typing status
      if (data.from === recipient) {
        setTyping(false);
      }
    });
    
    // Handle user status updates
    socketRef.current.on('userStatusUpdate', (data) => {
      console.log('User status update:', data);
      
      // Update online users list
      if (data.online) {
        setOnlineUsers(prev => {
          if (!prev.includes(data.username)) {
            return [...prev, data.username];
          }
          return prev;
        });
      } else {
        setOnlineUsers(prev => prev.filter(user => user !== data.username));
      }
      
      // Update recipient status if this is about the current recipient
      if (data.username === recipient) {
        setRecipientStatus(prev => ({
          ...prev,
          online: data.online
        }));
      }
      
      // Add system message
      setMessages(prevMessages => [
        ...prevMessages,
        {
          from: 'System',
          message: `${data.username} is ${data.online ? 'online' : 'offline'}`,
          timestamp: Date.now(),
          system: true
        }
      ]);
    });
    
    // Handle typing indicators
    socketRef.current.on('userTyping', (data) => {
      if (data.username === recipient) {
        setTyping(true);
        
        // Clear any existing timeout
        if (typingTimeoutRef.current) {
          clearTimeout(typingTimeoutRef.current);
        }
        
        // Set a timeout to clear the typing indicator
        typingTimeoutRef.current = setTimeout(() => {
          setTyping(false);
        }, 3000);
      }
    });
    
    // Handle pong responses
    socketRef.current.on('pong', (data) => {
      const latency = Date.now() - data.timestamp;
      console.log(`Pong received, latency: ${latency}ms`);
      
      // Update connection details with latency
      setConnectionDetails(prev => ({
        ...prev,
        latency
      }));
    });
    
    // Handle relay information updates
    socketRef.current.on('relayInfo', (data) => {
      console.log('Relay info update:', data);
      
      // Update connection details with relay information
      setConnectionDetails(prev => ({
        ...prev,
        ...data
      }));
    });
  };

  const getOnlineUsers = () => {
    if (socketRef.current) {
      socketRef.current.emit('getOnlineUsers', {}, (response) => {
        if (response && response.users) {
          setOnlineUsers(response.users);
        }
      });
    }
  };

  const handleLogin = (e) => {
    e.preventDefault();
    
    if (!username.trim()) {
      setSecurityAlert({
        username: 'System',
        message: 'Please enter a username',
        type: 'error'
      });
      return;
    }
    
    if (!deviceId) {
      setSecurityAlert({
        username: 'System',
        message: 'Device ID not generated yet. Please wait a moment and try again.',
        type: 'error'
      });
      return;
    }
    
    if (encryptionStatus !== 'ready') {
      setSecurityAlert({
        username: 'System',
        message: 'Encryption not ready. Please wait a moment and try again.',
        type: 'error'
      });
      return;
    }
    
    // Connect to the relay server
    connectToBaseNode();
  };

  const handleUsernameChange = (e) => {
    const newUsername = e.target.value;
    setUsername(newUsername);
    
    // Check username availability
    if (newUsername.trim().length > 2) {
      setIsCheckingUsername(true);
      
      // Debounce the check
      const checkTimeout = setTimeout(() => {
        if (socketRef.current) {
          socketRef.current.emit('checkUsername', { username: newUsername }, (response) => {
            setIsCheckingUsername(false);
            setUsernameAvailable(!response.exists);
          });
        } else {
          // If not connected, make a direct API call
          axios.get(`${BASE_NODE_URL}/check-username?username=${newUsername}`)
            .then(response => {
              setIsCheckingUsername(false);
              setUsernameAvailable(!response.data.exists);
            })
            .catch(error => {
              console.error('Error checking username:', error);
              setIsCheckingUsername(false);
              setUsernameAvailable(true); // Assume available on error
            });
        }
      }, 500);
      
      return () => clearTimeout(checkTimeout);
    } else {
      setIsCheckingUsername(false);
      setUsernameAvailable(true);
    }
  };

  const handleRecipientChange = (e) => {
    const newRecipient = e.target.value;
    setRecipient(newRecipient);
    
    // Check if recipient exists
    if (newRecipient.trim().length > 2 && socketRef.current) {
      socketRef.current.emit('checkRecipient', { username: newRecipient }, (response) => {
        setRecipientStatus({
          exists: response.exists,
          online: response.online
        });
      });
    } else {
      setRecipientStatus({ exists: false, online: false });
    }
  };

  const handleSendMessage = (e) => {
    e.preventDefault();
    
    if (!socketRef.current || !connected) {
      setSecurityAlert({
        username: 'System',
        message: 'Not connected to server',
        type: 'error'
      });
      return;
    }
    
    if (!recipient) {
      setSecurityAlert({
        username: 'System',
        message: 'Please enter a recipient',
        type: 'error'
      });
      return;
    }
    
    if (!message.trim()) {
      return;
    }
    
    const sendMessageAsync = async () => {
      let messageToSend = message;
      let encrypted = false;
      let publicKey = null;
      
      // Check if we should encrypt the message
      if (encryptionEnabled && publicKeys[recipient] && publicKeys[recipient].key) {
        try {
          messageToSend = await encryptMessage(message, publicKeys[recipient].key);
          encrypted = true;
          console.log('Message encrypted successfully');
        } catch (error) {
          console.error('Failed to encrypt message:', error);
          setSecurityAlert({
            username: 'System',
            message: 'Failed to encrypt message. Sending as plaintext.',
            type: 'warning'
          });
        }
      } else if (encryptionEnabled) {
        // We don't have the recipient's public key yet
        publicKey = keyPair.publicKey;
        console.log('Sending public key with message');
      }
      
      // Send the message
      socketRef.current.emit('sendMessage', {
        to: recipient,
        message: messageToSend,
        deviceId,
        encrypted,
        publicKey,
        timestamp: Date.now()
      }, (response) => {
        console.log('Message send response:', response);
        
        if (response && response.delivered) {
          // Add message to state
          setMessages(prevMessages => [
            ...prevMessages,
            {
              from: username,
              to: recipient,
              message: message, // Store the original message
              timestamp: Date.now(),
              sent: true,
              encrypted
            }
          ]);
          
          // Clear message input
          setMessage('');
        } else {
          if (response && response.queued) {
            setSecurityAlert({
              username: 'System',
              message: `Message to ${recipient} will be delivered when they come online (expires in 4 hours)`,
              type: 'info'
            });
          } else {
            const errorMsg = response?.reason || 'Message delivery failed';
            setSecurityAlert({
              username: 'System',
              message: `Failed to send message: ${errorMsg}`,
              type: 'error'
            });
          }
        }
      });
    };
    
    sendMessageAsync();
  };

  const handleMessageChange = (e) => {
    setMessage(e.target.value);
    
    // Send typing indicator
    if (socketRef.current && recipient && e.target.value.length > 0) {
      socketRef.current.emit('typing', { to: recipient });
    }
  };

  const getTimestamp = () => {
    const now = new Date();
    return `[${now.getHours().toString().padStart(2, '0')}:${now.getMinutes().toString().padStart(2, '0')}:${now.getSeconds().toString().padStart(2, '0')}]`;
  };

  const formatMessageTime = (timestamp) => {
    if (!timestamp) return getTimestamp();
    const date = new Date(timestamp);
    return `[${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}:${date.getSeconds().toString().padStart(2, '0')}]`;
  };

  return (
    <div style={{ 
      background: '#0a0e14', 
      minHeight: '100vh', 
      color: '#a2aabc', 
      display: 'flex', 
      alignItems: 'center', 
      justifyContent: 'center',
      fontFamily: '"Fira Code", monospace'
    }}>
      <div style={{ 
        background: '#171c28', 
        padding: 32, 
        borderRadius: 8, 
        width: '90%', 
        maxWidth: 800,
        boxShadow: '0 4px 20px rgba(0, 0, 0, 0.3)',
        border: '1px solid rgba(0, 255, 170, 0.3)'
      }}>
        <div style={{ 
          display: 'flex', 
          justifyContent: 'space-between', 
          alignItems: 'center',
          marginBottom: 24 
        }}>
          <h2 style={{ 
            margin: 0, 
            color: '#5ccfe6', 
            fontFamily: '"Fira Code", monospace',
            letterSpacing: '1px'
          }}>WhisperNet_</h2>
          <div style={{ 
            fontSize: 12, 
            padding: '4px 8px', 
            borderRadius: 4, 
            background: relayStatus === 'online' ? '#1c4b3c' : '#4b1c1c',
            color: relayStatus === 'online' ? '#5ccfe6' : '#ff8f40',
            cursor: 'pointer'
          }} onClick={() => setShowConnectionInfo(!showConnectionInfo)}>
            {relayStatus === 'online' ? 'Base Node Online' : 
             relayStatus === 'checking' ? 'Checking...' : 'Base Node Offline'}
          </div>
        </div>
        
        {showConnectionInfo && (
          <div style={{ 
            background: '#0d1117', 
            padding: 12, 
            borderRadius: 4, 
            fontSize: 12, 
            fontFamily: 'monospace',
            marginBottom: 16,
            border: '1px solid #1e2d3d'
          }}>
            <div>Status: {status}</div>
            {connected && (
              <>
                <div>Socket ID: {connectionDetails.socketId || 'unknown'}</div>
                <div>Transport: {connectionDetails.transport || 'unknown'}</div>
                <div>Base Node: {connectionDetails.baseNodeUrl || BASE_NODE_URL}</div>
                <div>Relay ID: <span style={{ color: '#5ccfe6' }}>{relayServerUrl || 'Unknown'}</span></div>
                <div>Relay Status: <span style={{ 
                  color: connectionDetails.relayStatus === 'connected_to_base' ? '#bae67e' : 
                         connectionDetails.relayStatus === 'direct_to_base' ? '#5ccfe6' : '#ff8f40' 
                }}>
                  {connectionDetails.relayStatus === 'connected_to_base' ? 'Connected to Base' : 
                   connectionDetails.relayStatus === 'direct_to_base' ? 'Direct to Base Node' : 
                   connectionDetails.relayStatus === 'assigned_by_base' ? 'Assigned by Base' : 'Standalone'}
                </span></div>
                {connectionDetails.connectedUsers !== undefined && (
                  <div>Users on Relay: {connectionDetails.connectedUsers}</div>
                )}
                {connectionDetails.ip && connectionDetails.port && (
                  <div>Relay Address: {connectionDetails.ip}:{connectionDetails.port}</div>
                )}
              </>
            )}
            <div>Base Node Status: <span style={{
              color: relayStatus === 'online' ? '#bae67e' : '#ff8f40'
            }}>{relayStatus}</span></div>
            {deviceId && <div>Device ID: {deviceId.substring(0, 8)}...</div>}
            {connected && onlineUsers.length > 0 && (
              <div style={{ marginTop: 8 }}>Online Users: {onlineUsers.join(', ')}</div>
            )}
          </div>
        )}
        
        {securityAlert && (
          <div style={{ 
            background: securityAlert.type === 'success' ? '#1c4b2c' : 
                       securityAlert.type === 'info' ? '#1c2c4b' : '#4b1c1c', 
            color: securityAlert.type === 'success' ? '#8fff40' : 
                  securityAlert.type === 'info' ? '#40a9ff' : '#ff8f40', 
            padding: 12, 
            borderRadius: 4, 
            marginBottom: 16,
            position: 'relative',
            fontSize: 14
          }}>
            <div style={{ marginRight: 20 }}>{securityAlert.message}</div>
            
            {/* Action buttons if available */}
            {securityAlert.action === 'switchToRelay' && (
              <div style={{ marginTop: 8 }}>
                <button 
                  style={{ 
                    background: '#1c4b2c', 
                    color: '#8fff40', 
                    border: 'none', 
                    padding: '4px 8px', 
                    borderRadius: 4, 
                    marginRight: 8,
                    cursor: 'pointer'
                  }}
                  onClick={() => dismissAlert('switchToRelay', securityAlert.actionData)}
                >
                  Switch to Relay
                </button>
                <button 
                  style={{ 
                    background: '#4b1c1c', 
                    color: '#ff8f40', 
                    border: 'none', 
                    padding: '4px 8px', 
                    borderRadius: 4,
                    cursor: 'pointer'
                  }}
                  onClick={() => dismissAlert('dismiss')}
                >
                  Stay on Base Node
                </button>
              </div>
            )}
            
            <button 
              style={{ 
                position: 'absolute', 
                top: 8, 
                right: 8, 
                background: 'none', 
                border: 'none', 
                color: securityAlert.type === 'success' ? '#8fff40' : 
                      securityAlert.type === 'info' ? '#40a9ff' : '#ff8f40', 
                cursor: 'pointer',
                fontSize: 16
              }}
              onClick={() => dismissAlert('dismiss')}
            >
              ×
            </button>
          </div>
        )}
        
        {!connected ? (
          <div>
            <form onSubmit={handleLogin} style={{ marginBottom: 16 }}>
              <div style={{ marginBottom: 16 }}>
                <label style={{ display: 'block', marginBottom: 8, color: '#5ccfe6' }}>
                  Username:
                </label>
                <input 
                  type="text" 
                  value={username} 
                  onChange={handleUsernameChange} 
                  style={{ 
                    width: '100%', 
                    padding: '8px 12px', 
                    background: '#0d1117', 
                    border: '1px solid #1e2d3d', 
                    borderRadius: 4, 
                    color: '#a2aabc',
                    fontFamily: '"Fira Code", monospace'
                  }} 
                  placeholder="Enter username"
                  disabled={isCheckingUsername}
                />
                {username.trim().length > 2 && (
                  <div style={{ 
                    fontSize: 12, 
                    marginTop: 4, 
                    color: isCheckingUsername ? '#a2aabc' : (usernameAvailable ? '#bae67e' : '#ff8f40')
                  }}>
                    {isCheckingUsername ? 'Checking availability...' : 
                     (usernameAvailable ? 'Username available' : 'Username already taken')}
                  </div>
                )}
              </div>
              
              <div style={{ 
                display: 'flex', 
                justifyContent: 'space-between', 
                alignItems: 'center'
              }}>
                <button 
                  type="submit" 
                  style={{ 
                    padding: '8px 16px', 
                    background: '#1c4b3c', 
                    color: '#5ccfe6', 
                    border: 'none', 
                    borderRadius: 4, 
                    cursor: 'pointer',
                    fontFamily: '"Fira Code", monospace'
                  }}
                  disabled={!usernameAvailable || isCheckingUsername || !username.trim() || encryptionStatus !== 'ready'}
                >
                  {relayStatus === 'checking' ? 'Checking...' : 'CONNECT'}
                </button>
                
                <div style={{ 
                  fontSize: 12, 
                  color: encryptionStatus === 'ready' ? '#bae67e' : 
                         encryptionStatus === 'generating' ? '#f0c674' : '#ff8f40'
                }}>
                  Encryption: {encryptionStatus === 'ready' ? 'Ready' : 
                              encryptionStatus === 'generating' ? 'Generating keys...' : 
                              encryptionStatus === 'initializing' ? 'Initializing...' : 'Error'}
                </div>
              </div>
            </form>
            
            {relayStatus === 'offline' && (
              <div style={{ textAlign: 'center', marginTop: 16 }}>
                <button 
                  onClick={retryConnection}
                  style={{ 
                    padding: '6px 12px', 
                    background: '#1c2c4b', 
                    color: '#5ccfe6', 
                    border: 'none', 
                    borderRadius: 4, 
                    cursor: 'pointer',
                    fontFamily: '"Fira Code", monospace',
                    fontSize: 12
                  }}
                >
                  Retry Connection
                </button>
              </div>
            )}
          </div>
        ) : (
          <>
            <div style={{ 
              background: '#0d1117', 
              padding: 16, 
              borderRadius: 4, 
              marginBottom: 16, 
              maxHeight: 400, 
              overflowY: 'auto',
              border: '1px solid #1e2d3d'
            }}>
              {messages.length === 0 ? (
                <div style={{ 
                  color: '#636b78', 
                  textAlign: 'center', 
                  padding: 20 
                }}>
                  No messages yet. Start a conversation!
                </div>
              ) : (
                messages.map((msg, index) => (
                  <div 
                    key={index} 
                    style={{ 
                      marginBottom: 8,
                      padding: 8,
                      borderRadius: 4,
                      background: msg.system ? 'transparent' : 
                                msg.from === username ? '#1c2c4b' : '#1c2c2c'
                    }}
                  >
                    <div style={{ 
                      fontSize: 12, 
                      marginBottom: 4, 
                      color: msg.system ? '#636b78' : 
                             msg.from === 'System' ? '#f0c674' : 
                             msg.from === username ? '#5ccfe6' : '#ff8f40',
                      display: 'flex',
                      justifyContent: 'space-between'
                    }}>
                      <span>
                        {msg.from}{msg.to ? ` → ${msg.to}` : ''}
                        {msg.encrypted && (
                          <span style={{ 
                            marginLeft: 8, 
                            color: msg.decryptionStatus === 'decrypted' ? '#bae67e' : 
                                  msg.decryptionStatus === 'decrypting' ? '#f0c674' : '#ff8f40',
                            fontSize: 10
                          }}>
                            {msg.decryptionStatus === 'decrypted' ? '🔒 encrypted' : 
                             msg.decryptionStatus === 'decrypting' ? '🔄 decrypting...' : 
                             msg.decryptionStatus === 'failed' ? '🔒 cannot decrypt' : '🔒 encrypted'}
                          </span>
                        )}
                        {msg.bounced && (
                          <span style={{ marginLeft: 8, color: '#f0c674', fontSize: 10 }}>
                            ↩️ delayed
                          </span>
                        )}
                      </span>
                      <span style={{ color: '#636b78' }}>
                        {formatMessageTime(msg.timestamp)}
                      </span>
                    </div>
                    <div style={{ 
                      wordBreak: 'break-word',
                      color: msg.system ? '#636b78' : '#a2aabc'
                    }}>
                      {msg.message}
                    </div>
                  </div>
                ))
              )}
              <div ref={messagesEndRef} />
            </div>
            
            <form onSubmit={handleSendMessage}>
              <div style={{ marginBottom: 16 }}>
                <label style={{ display: 'block', marginBottom: 8, color: '#5ccfe6' }}>
                  Recipient:
                </label>
                <input 
                  type="text" 
                  value={recipient} 
                  onChange={handleRecipientChange} 
                  style={{ 
                    width: '100%', 
                    padding: '8px 12px', 
                    background: '#0d1117', 
                    border: '1px solid #1e2d3d', 
                    borderRadius: 4, 
                    color: '#a2aabc',
                    fontFamily: '"Fira Code", monospace'
                  }} 
                  placeholder="Enter recipient username"
                />
                {recipient.trim().length > 2 && (
                  <div style={{ 
                    fontSize: 12, 
                    marginTop: 4, 
                    color: recipientStatus.exists ? 
                          (recipientStatus.online ? '#bae67e' : '#f0c674') : 
                          '#ff8f40'
                  }}>
                    {recipientStatus.exists ? 
                      (recipientStatus.online ? 'User is online' : 'User exists but is offline') : 
                      'User not found'}
                  </div>
                )}
              </div>
              
              <div style={{ marginBottom: 16 }}>
                <label style={{ display: 'block', marginBottom: 8, color: '#5ccfe6' }}>
                  Message:
                </label>
                <div style={{ position: 'relative' }}>
                  <textarea 
                    value={message} 
                    onChange={handleMessageChange} 
                    style={{ 
                      width: '100%', 
                      padding: '8px 12px', 
                      background: '#0d1117', 
                      border: '1px solid #1e2d3d', 
                      borderRadius: 4, 
                      color: '#a2aabc',
                      fontFamily: '"Fira Code", monospace',
                      minHeight: 80,
                      resize: 'vertical'
                    }} 
                    placeholder="Type your message here"
                  />
                  {typing && (
                    <div style={{ 
                      position: 'absolute', 
                      bottom: 8, 
                      right: 8, 
                      fontSize: 12, 
                      color: '#f0c674' 
                    }}>
                      {recipient} is typing...
                    </div>
                  )}
                </div>
              </div>
              
              <div style={{ 
                display: 'flex', 
                justifyContent: 'space-between', 
                alignItems: 'center'
              }}>
                <button 
                  type="submit" 
                  style={{ 
                    padding: '8px 16px', 
                    background: '#1c4b3c', 
                    color: '#5ccfe6', 
                    border: 'none', 
                    borderRadius: 4, 
                    cursor: 'pointer',
                    fontFamily: '"Fira Code", monospace'
                  }}
                  disabled={!recipient.trim() || !message.trim()}
                >
                  SEND
                </button>
                
                <button 
                  type="button" 
                  style={{ 
                    padding: '8px 16px', 
                    borderRadius: 4, 
                    background: '#4b1c1c', 
                    color: '#ff8f40', 
                    fontSize: 14, 
                    border: 'none',
                    cursor: 'pointer',
                    fontFamily: '"Fira Code", monospace'
                  }} 
                  onClick={handleDisconnect}
                >
                  DISCONNECT
                </button>
                
                <div style={{ 
                  fontSize: 12, 
                  color: '#636b78', 
                  display: 'flex', 
                  alignItems: 'center' 
                }}>
                  <div style={{ 
                    width: 8, 
                    height: 8, 
                    borderRadius: '50%', 
                    background: connected ? '#bae67e' : '#ff8f40',
                    marginRight: 6
                  }}></div>
                  {status.includes('Connected') || status.includes('Registered') ? 'SECURE CONNECTION' : 'CONNECTION LOST'}
                </div>
              </div>
            </form>
          </>
        )}
      </div>
    </div>
  );
}

export default App;